<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>系统管理</el-breadcrumb-item>
      <el-breadcrumb-item>管理员管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-tabs v-model="activeName">
        <el-tab-pane label="查看" name="show">
          <el-table :data="tableData">
            <el-table-column label="id" prop="id" align="center"></el-table-column>
            <el-table-column label="账号" prop="username" align="center"></el-table-column>
            <el-table-column label="状态" prop="status" align="center">
              <template #default="o">{{o.row.status==1?"白名单":"黑名单"}}</template>
            </el-table-column>
            <el-table-column label="上次登录时间" prop="login_time" align="center"></el-table-column>
            <el-table-column label="创建时间" prop="create_time" align="center"></el-table-column>
            <el-table-column label="操作">
              <template #default="o">
                <el-button
                  type="text"
                  v-if="o.row.status==1"
                  @click="handleChange(o.row.id,0)"
                >加入黑名单</el-button>
                <el-button type="text" v-else @click="handleChange(o.row.id,1)">加入白名单</el-button>
                <el-button type="text" @click="handleDelete(o.row.id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" align="middle" style="height:40px">
            <el-pagination
              background
              layout="prev, pager, next"
              :page-size.sync="pageSize"
              :total="total"
              :current-page.sync="page"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <!-- 添加操作  -->
        <el-tab-pane label="添加" name="add">
          <el-col :span="8">
            <el-form label-width="80px" :model="addForm" :rules="rules" ref="addForm">
              <el-form-item label="账号" prop="username">
                <el-input placeholder="请输入账号" v-model="addForm.username"></el-input>
              </el-form-item>
              <el-form-item label="状态">
                <el-radio :label="0" v-model="addForm.status" prop="status">黑名单</el-radio>
                <el-radio :label="1" v-model="addForm.status" prop="status">白名单</el-radio>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd">新增</el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: "show",
      tableData: [],
      url: "/manager",
      total: 0,
      pageSize: 5,
      page: 1,
      // 新增对象
      addForm: {
        username: "",
        status: 1
      },
      rules: {
        username: { required: true, message: "请输入用户名", trigger: "blur" }
      }
    };
  },
  methods: {
    // 获取渲染数据
    fetchData() {
      this.$http
        .get(this.url, {
          params: {
            page: this.page,
            pageSize: this.pageSize,
            role: 2
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          } else {
            this.$message.error("获取失败");
          }
        });
    },
    // 添加操作
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code === 200) {
              this.$message.success("添加成功");
              // 重置清空数据
              this.$refs.addForm.resetFields();
              // console.log(this.resetFields())
              // 跳转到查看数据
              this.activeName = "show";
              // 刷新获取添加成功后的数据
              this.fetchData();
            }
          });
        } else {
          this.$message.error("添加失败");
        }
      });
    },
    // 修改状态
    handleChange(id, newStatus) {
      this.$http.put(this.url + "/" + id, { status: newStatus }).then(res => {
        console.log(res)
        if (res.data.code === 200) {
          this.$message.success("修改成功");
          // 重新获取数据
          this.fetchData();
        } else {
          this.$message.error("修改失败");
        }
      });
    },
    // 删除操作
    handleDelete(id) {
      this.$confirm("此操作将删除当前账户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$http.delete(this.url + "/" + id).then(res => {
            if (res.data.code === 200) {
              this.$message.success("删除成功");
              this.fetchData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  },
  watch: {
    page(newValue, oldValue) {
      this.fetchData();
    }
  },
  // 进来触发
  mounted() {
    this.fetchData();
  }
};
</script>

<style lang="scss" scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
</style>