<template>
  <div class='users'>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>系统管理</el-breadcrumb-item>
      <el-breadcrumb-item>顾客管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data="tableData">
        <el-table-column prop="id" label="id">
        </el-table-column>
        <el-table-column prop="nickName" label="昵称">

        </el-table-column>
        <el-table-column prop="avatarUrl" label="头像">
          <template #default="o">
            <img :src="o.row.avatarUrl" alt="" style="height:120px">
          </template>
        </el-table-column>
        <el-table-column prop="gender" label="性别">
          <template #default="o">
            <span v-if="o.row.gender===1">男</span>
            <span v-else>女</span>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间">
          
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="center" align="middle" style="height:40px">
      <el-pagination 
      background
      layout="prev,pager,next"
      :total="total"
      :page-size.sync="pageSize"
      :current-page.sync="page"
      >
      </el-pagination>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'user',
  data(){
    return {
      tableData: [],
      page:1,
      pageSize:1,
      total:0
    }
  },
  components: {},
  created(){},
  mounted(){
    // this.$router 路由对象 调用 push go replace可以实现路由的跳转
    // this.$route 路由信息对象，包含当前匹配到的路由对象的所有信息
    this.fetchData();
  },
  methods: {
    fetchData(){
      this.$http.get("/users",{
        params: {
          page: this.page,
          pageSize: this.pageSize,
        }
      }).then(res=>{
        this.tableData = res.data.data;
        this.total = res.data.total;
      })
    }
  },
  watch: {
    page(newValue,oldValue) {
      this.fetchData();
    }
  }
}
</script>
<style lang='scss' scoped>
</style>
