<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>系统管理</el-breadcrumb-item>
      <el-breadcrumb-item>密码修改</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-col :span="8">
        <el-form
          label-width="100px"
          :model="passwordForm"
          :rules="rules"
          :status-icon="true"
          ref="passwordForm"
        >
          <el-form-item label="原始密码" prop="oldPassword">
            <el-input type="password" v-model="passwordForm.oldPassword"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop="newPassword">
            <el-input type="password" v-model="passwordForm.newPassword"></el-input>
          </el-form-item>
          <el-form-item label="确认新密码" prop="newPassword2">
            <el-input type="password" v-model="passwordForm.newPassword2"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="handlechang">提交修改</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "password",
  data() {
    let passwordValidator = (rule, value, callback) => {
      let reg = /^[0-9a-zA-Z]{6,}$/;
      if (reg.test(value)) {
        callback();
      } else {
        callback(new Error("密码必须为6为以上的字母或数字组成 "));
      }
    };
    let passwordValidator2 = (rule, value, callback) => {
      if (value === this.passwordForm.newPassword) {
        callback();
      } else {
        callback(new Error("两次密码输入必须保持一致"));
      }
    };
    return {
      passwordForm: {
        id: sessionStorage.getItem("id"),
        oldPassword: "",
        newPassword: "",
        newPassword2: ""
      },
      rules: {
        oldPassword: {
          required: true,
          message: "请输入原始密码",
          trigger: "blur"
        },
        newPassword: [
          { required: true, message: "请输入新密码", trigger: "blur" },
          { validator: passwordValidator, trigger: "blur" }
        ],
        newPassword2: [
          { required: true, message: "重新输入新密码", trigger: "blur" },
          { validator: passwordValidator2, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    handlechang() {
      this.$refs.passwordForm.validate(v => {
        if (v) {
          this.$http
            .post("/api/admin/changePassword", this.passwordForm)
            .then(res => {
              if (res.data.code === 200) {
                this.$refs.passwordForm.resetFields();
                this.$message.success("修改成功");
              } else {
                this.$message.error(res.data.msg);
              }
            });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  height: 450px;
}
</style>